import React from 'react'
import { Navbar, Nav } from 'react-bootstrap'
import { NavLink } from 'react-router-dom'

import './Navbar.css'

const NavbarComponent = () => {

    return (
        <Navbar expand="lg">
            <Navbar.Toggle aria-controls="basic-navbar-nav" />
            <Navbar.Collapse id="responsive-navbar-nav">
                    <NavLink className="nav-link logo" to="/">Max<span className="restaurant">Restaurant</span></NavLink>
                <Nav className="mr-auto">
                    <NavLink to="/menu" className="nav-link link-style">Menu</NavLink>
                    <NavLink to="/about" className="nav-link link-style">About</NavLink>
                </Nav>
                <Nav>
                    <NavLink to="/reservations" className="nav-link link-style">Reservations</NavLink>
                </Nav>
            </Navbar.Collapse>
        </Navbar>
    )
}

export default NavbarComponent